import React, { useState } from 'react';
import PageLayout from '../components/Layout/PageLayout';
import { notices } from '../data/notices';

const Notice = () => {
  const [selectedNotice, setSelectedNotice] = useState(null);

  return (
    <PageLayout title="공지사항">
      <div className="space-y-4">
        {!selectedNotice && (
          <section className="bg-white rounded-lg shadow-md overflow-hidden">
            {notices.map((notice) => (
              <div 
                key={notice.id}
                className="p-4 cursor-pointer hover:bg-gray-50 transition-colors border-b last:border-b-0"
                onClick={() => setSelectedNotice(notice)}
              >
                <div className="flex flex-col md:flex-row md:justify-between md:items-center gap-2">
                  <h3 className="text-xl text-gray-800">{notice.title}</h3>
                  <span className="text-gray-500 text-sm md:text-base">{notice.date}</span>
                </div>
              </div>
            ))}
          </section>
        )}

        {selectedNotice && (
          <section className="bg-white p-6 rounded-lg shadow-md">
            <div className="border-b border-gray-200 pb-4 mb-4">
              <h3 className="text-2xl font-semibold text-primary mb-2">
                {selectedNotice.title}
              </h3>
              <p className="text-gray-500">{selectedNotice.date}</p>
            </div>
            <div className="text-gray-800 text-lg whitespace-pre-line">
              {selectedNotice.content}
            </div>
            <button 
              onClick={() => setSelectedNotice(null)}
              className="mt-4 text-accent hover:text-primary transition-colors"
            >
              목록으로 돌아가기
            </button>
          </section>
        )}
      </div>
    </PageLayout>
  );
};

export default Notice;